import { format } from "date-fns-tz"
import { getTimePassed } from "@rp/common/utils"
import { useDeviceInfo } from "@rp/react/web/common/hooks"
import { Tooltip, Typography } from "@rp/react/web/mui/core"

type Props = {
  timestamp: string
  color?: string
}

function DocumentDate({ timestamp, color }: Props) {
  const { isMobile, isHoverDevice } = useDeviceInfo()
  return (
    <Tooltip
      title={
        isMobile || isHoverDevice
          ? ""
          : format(new Date(timestamp), "hh:mm a zzz Â· MMM dd, yyyy", {})
      }
      arrow
      placement="top"
      disableHoverListener={isMobile || !isHoverDevice}
      disableInteractive
      disableTouchListener
      disableFocusListener
    >
      <Typography
        sx={theme => ({
          fontSize: 12,
          letterSpacing: 1,
          whiteSpace: "nowrap",
          color: color || theme.palette.grey[300],
        })}
        component="span"
        data-testid="document-date"
      >
        {getTimePassed(timestamp)}
      </Typography>
    </Tooltip>
  )
}

export { DocumentDate }
